<template>
  <transition
    name="backdrop"
    v-on:before-enter="_beforeEnter"
    v-on:after-leave="_afterLeave">
    <div class="ion-backdrop"
         @click="onBdClick"
         :class="{'backdrop-no-tappable':!enableBackdropDismiss}"
         :style="positionLocal"
         v-show="isActiveLocal"></div>
  </transition>
</template>
<script type="text/ecmascript-6">
  import { urlChange } from '../../util/dom';
  export default{
    name: 'Backdrop',
    data(){
      return {
        // 
        isActiveLocal: false, // present/dismiss
        bdClickLocal: this.bdClick,
        positionLocal: this.position,
        isOpen: false, // backdrop, isActiveLocal

        count: 0, // 
      }
    },
    props: {
      /**
       * , 
       * , : present/dismissenableBackdropDismiss
       * */
      enableBackdropDismiss: {
        type: Boolean,
        default: true
      },
      /**
       * backdrop
       * */
      isActive: {
        type: Boolean,
        default: false
      },
      /**
       * 
       * */
      bdClick: {
        type: Function,
        default(){
          return function () {}
        }
      },
      /**
       * backdrop, 
       * */
      position: {
        type: Object,
        default: null,
      },
    },
    watch: {
      isActive () {
        this.isOpen = this.isActiveLocal = this.isActive;
      },
      position () {
        this.positionLocal = this.position;
      },
    },
    methods: {
      /**
       * 
       * @private
       */
      _beforeEnter (el) {
        this.$emit('$backdropShown');
        this.$eventBus.$emit('$backdropShown');
      },
      _afterLeave (el) {
        this.$emit('$backdropHidden');
        this.$eventBus.$emit('$backdropHidden');
      },
      _getPosition(){
        this.position = {}
      },

      /**
       * backdrop, enableBackdropDismiss
       * */
      onBdClick(){
        if (!!this.bdClickLocal) {
          this.bdClickLocal();
        }
      },

      /**
       * backdrop
       *
       * , ,
       * , dismiss()
       *
       * @param {object} options - 
       * @example
       * {
       *    bdClick:Function
       *    position:{
       *      top:String/'10px',
       *      right:String/'10px',
       *      bottom:String/'10px',
       *      left:String/'10px',
       *    }
       * }
       * */
      present (options = null) {
        const _this = this;
        _this.count++;
        if (_this.count > 1) {
          return
        }

        if (!!options) {
          if (!!options.bdClick && typeof options.bdClick === 'function') {
            _this.bdClickLocal = options.bdClick;
          }
          if (!!options.position) {
            if (!!options.position['right']) {
              options.position['left'] = 'auto'
            }
            if (!!options.position['bottom']) {
              options.position['top'] = 'auto'
            }
            _this.positionLocal = options.position;
          } else {
            _this.positionLocal = null;
          }
        } else {
          _this.bdClickLocal = null;
          _this.positionLocal = null;
        }

        _this.isOpen = _this.isActiveLocal = true;

        // url
        urlChange(function () {
          _this.dismiss()
        })
      },

      /**
       * backdrop
       * */
      dismiss () {
        if (this.count > 1) {
          this.count--;
          return
        }
        this.count--;
        this.isOpen = this.isActiveLocal = false;
      },
    },
  }
</script>
<style lang="scss">
  @import './backdrop';
  // transition
  @import '../../transitions/backdrop';
</style>



