<template>
  <Page>
    <Header>
      <Navbar>
        <Title>Action Sheet</Title>
      </Navbar>
    </Header>
    <Content padding>

      <Button type="block" @click="showActionSheet1()" color="primary">
        Basic Action Sheet
      </Button>

      <Button type="block" @click="showActionSheet2()" color="primary">
        No Cancel Action Sheet
      </Button>

    </Content>
  </Page>
</template>

<!--actionsheet-->
<script type="text/ecmascript-6">
  export default{
    name: 'action-sheet',
    data () {
      return {}
    },
    methods: {
      showActionSheet1 () {
        const _this = this;
       _this.$actionSheet.present({
          title: '',
          subTitle: '',
          cssClass: '  ActionSheetCssClass1 ActionSheetCssClass2  ',
          enableBackdropDismiss: true,
          buttons: [
            {
              text: '',
              role: 'destructive',
              icon: 'icon-Destructive',
              cssClass: '  DestructiveBtnCssClass1 DestructiveBtnCssClass2 ',
              handler: () => {
                console.log('Destructive clicked');
              }
            },
            {
              text: '',
              handler: () => {
                console.log('Archive1 clicked');
              }
            },
            {
              text: '',
              handler: () => {
                console.log('Archive4 clicked');
              }
            },
            {
              text: '',
              role: 'cancel',
              handler: () => {
                _this.$actionSheet.dismiss().then(function (data) {
                  console.debug('promise')
                });
              }
            }
          ]
        })
      },
      showActionSheet2 () {
        const _this = this;
       _this.$actionSheet.present({
          title: '',
          subTitle: ', backdrop',
          cssClass: '  ActionSheetCssClass1 ActionSheetCssClass2  ',
          enableBackdropDismiss: false,
          buttons: [
            {
              text: 'MacBook Pro',
              role: 'destructive',
              icon: 'icon-Destructive',
              cssClass: '  DestructiveBtnCssClass1 DestructiveBtnCssClass2 ',
              handler: () => {
                console.log('Destructive clicked');
              }
            },
            {
              text: 'MacMini',
              handler: () => {
                console.log('Archive1 clicked');
              }
            },
            {
              text: 'iPhone',
              handler: () => {
                console.log('Archive4 clicked');
              }
            }
          ]
        });
      },
    },

  }

</script>

<style lang="scss"></style>



