<template>
  <section>
    <slot></slot>
  </section>
</template>
<script type="text/ecmascript-6">
  /**
   * !!TabTabs!!
   *
   * Tab, :to, 
   *
   * , DOM
   *
   * : onSelect
   *
   * */
  export default{
    name: 'Tab',
    props: {
      /**
       * 
       * */
      enabled: {
        type: Boolean,
        default: true,
      },
      /**
       * 
       * */
      to: {
        type: String,
        default: '',
      },
      /**
       * 
       * */
      show: {
        type: Boolean,
        default: true,
      },
      /**
       * 
       * */
      tabBadge: {
        type: String,
        default: '',
      },
      /**
       * 
       * */
      tabBadgeStyle: {
        type: String,
        default: '',
      },
      /**
       * tabIconName
       * */
      tabIcon: {
        type: String,
        default: '',
      },
      /**
       * tabtabTitle
       * */
      tabTitle: {
        type: String,
        default: '',
      },

      // TODO: tabUrlPath
      // string The URL path name to represent this tab within the URL.

      //TODO: tabsHideOnSubPages
      // boolean If true, hide the tabs on child pages.
    },
    methods: {
      /**
       * Tab
       * Tab, !
       *
       * */
      getTabInfo(){

        let _tabId = this._uid;
        let hasTitle = !!this.tabTitle;
        let hasIcon = !!this.tabIcon;
        let hasTitleOnly = !!this.tabTitle && !this.tabIcon;
        let hasIconOnly = !!this.tabIcon && !this.tabTitle;
        let hasBadge = !!this.tabBadge;
        let disHover = VM.config.getBoolean('hoverCSS', false);

        return {
          _tabId,
          hasTitle,
          hasIcon,
          hasTitleOnly,
          hasIconOnly,
          hasBadge,
          disHover,

          enabled: this.enabled,
          to: eval("(" + this.to + ")"), // 
          show: this.show,
          tabBadge: this.tabBadge,
          tabBadgeStyle: this.tabBadgeStyle,
          tabIcon: this.tabIcon,
          tabTitle: this.tabTitle,
        }
      }
    }
  }
</script>



