<template>
  <Page>
    <Header>
      <Navbar>
        <Title>Loading</Title>
      </Navbar>
    </Header>
    <Content padding>
      <p>, Backdrop</p>
      <p>: ios/ios-small/bubbles/circles/crescent/dots</p>
      <Button type="block" @click="showDefault()">Loading</Button>
      <Button type="block" @click="showIos()"> ios</Button>
      <Button type="block" @click="showIosSmall()"> ios-small</Button>
      <Button type="block" @click="showBubbles()"> Bubbles</Button>
      <Button type="block" @click="showCircles()"> circles</Button>
      <Button type="block" @click="showCrescent()"> crescent</Button>
      <Button type="block" @click="showDots()"> dots</Button>

      <p>Backdrop</p>
      <Button type="block" @click="showNoBackDrop()">Backdrop</Button>


      <p>loading</p>
      <Button type="block" @click="showOther()"></Button>

      <p>(NoSpinner)</p>
      <Button type="block" @click="showNoSpinner()">showNoSpinner</Button>

      <p></p>
      <Button type="block" @click="showCusContent()">showCusContent</Button>
    </Content>
  </Page>
</template>
<style scoped lang="scss">
  .loadingDemo {
    padding: 15px;
  }
</style>
<script type="text/ecmascript-6">
  export default{
    data(){
      return {}
    },
    methods: {
      // 
      showDefault () {
        const _this = this;
        _this.$loading.present({
          content: ', 4000ms...',
          cssClass: 'cssClass',
          dismissOnPageChange:true, // urlloading
          showBackdrop: true,
        });
        setTimeout(function () {
          _this.$loading.dismiss().then(function () {
            console.debug('dismiss in promise success!')
          })
        },4000);
      },
      showIos () {
        const _this = this;
        _this.$loading.present({
          spinner: 'ios',
          content: ', 1000ms...',
          cssClass: 'cssClass',
          dismissOnPageChange:true, // urlloading
          showBackdrop: true,
        });
        setTimeout(function () {
          _this.$loading.dismiss().then(function () {
            console.debug('dismiss in promise success!')
          })
        },1000);
      },
      showIosSmall () {
        const _this = this;
        _this.$loading.present({
          spinner: 'ios-small',
          content: ', 1000ms...',
          cssClass: 'cssClass',
          dismissOnPageChange:true, // urlloading
          showBackdrop: true,
        });
        setTimeout(function () {
          _this.$loading.dismiss().then(function () {
            console.debug('dismiss in promise success!')
          })
        },1000);
      },
      showBubbles () {
        const _this = this;
        _this.$loading.present({
          spinner: 'bubbles',
          content: ', 1000ms...',
          cssClass: 'cssClass',
          dismissOnPageChange:true, // urlloading
          showBackdrop: true,
        });
        setTimeout(function () {
          _this.$loading.dismiss().then(function () {
            console.debug('dismiss in promise success!')
          })
        },1000);
      },
      showCircles () {
        const _this = this;
        _this.$loading.present({
          spinner: 'circles',
          content: ', 1000ms...',
          cssClass: 'cssClass',
          dismissOnPageChange:true, // urlloading
          showBackdrop: true,
        });
        setTimeout(function () {
          _this.$loading.dismiss().then(function () {
            console.debug('dismiss in promise success!')
          })
        },1000);
      },
      showCrescent () {
        const _this = this;
        _this.$loading.present({
          spinner: 'crescent',
          content: ', 1000ms...',
          cssClass: 'cssClass',
          dismissOnPageChange:true, // urlloading
          showBackdrop: true,
        });
        setTimeout(function () {
          _this.$loading.dismiss().then(function () {
            console.debug('dismiss in promise success!')
          })
        },1000);
      },
      showDots () {
        const _this = this;
        _this.$loading.present({
          spinner: 'dots',
          content: ', 1000ms...',
          cssClass: 'cssClass',
          dismissOnPageChange:true, // urlloading
          showBackdrop: true,
        });
        setTimeout(function () {
          _this.$loading.dismiss().then(function () {
            console.debug('dismiss in promise success!')
          })
        },1000);
      },

      // backdroploading
      showNoBackDrop () {
        const _this = this;
        _this.$loading.present({
          content: ', 1000ms...',
          cssClass: 'cssClass',
          dismissOnPageChange:true, // urlloading
          showBackdrop: false,
        });
        setTimeout(function () {
          _this.$loading.dismiss().then(function () {
            console.debug('dismiss in promise success!')
          })
        },1000);
      },

      // 
      showOther () {
        const _this = this;
        // 
        _this.$loading.present({
          content: ', , Backdrop.',
          cssClass: 'cssClass',
          showBackdrop: false,
        });

        // 
        setTimeout(function () {
          _this.$loading.present({
            content: ', , Backdrop.',
            cssClass: 'cssClass',
            showBackdrop: true,
          });

          // 
         setTimeout(function () {
           _this.$loading.present({
             content: ', , Backdrop.',
             cssClass: 'cssClass',
             showBackdrop: false,
           });
           setTimeout(function () {
             _this.$loading.dismiss().then(function () {
               console.debug('Loading');
             })
           },2000)
         },2000)
        },2000)
      },

      // spinner
      showNoSpinner () {
        const _this = this;
        _this.$loading.present({
          spinner: 'hide',
          content: ', loadingSpnner.',
          cssClass: 'cssClass',
          showBackdrop: true,
        });
        setTimeout(function () {
          _this.$loading.dismiss().then(function () {
            console.debug('dismiss in promise success!')
          })
        },1000);
      },

      // 
      showCusContent () {
        const _this = this;
        _this.$loading.present({
          spinner: 'hide',
          content: '<p>Vimo</p>',
          cssClass: 'cssClass',
          showBackdrop: true,
        });
        setTimeout(function () {
          _this.$loading.dismiss().then(function () {
            console.debug('dismiss in promise success!')
          })
        },1000);
      }
    },
  }
</script>



