<template>
    <div class="ion-segment segment" :class="[modeClass,colorClass]">
        <slot></slot>
    </div>
</template>
<style lang="scss">
    @import './segment';
    @import './segment.ios';
</style>
<script type="text/ecmascript-6">
    export default{
  name:'Segment',
        props: {
            /**
             * value
             * */
            value: {
                type: String,
                default: '',
            },
            /**
             * colorprimarysecondarydangerlightdark
             * */
            color: {
                type: String,
                default: '',
            },
            /**
             * mode  ios/window/android/we/alipay
             * */
            mode: {
                type: String,
                default:  VM.config.get('mode') || 'ios',
            },
        },
        data: function () {
            return {
                // valuepropsvalue
                parentVal: this.value
            }
        },
        watch: {
            // 
            value:function (val) {
                this.parentVal = val;
            },
            // 
            parentVal: function () {
                this.setChildrenSelectState();
            }
        },
        computed: {
            // 
            modeClass: function () {
                return `segment-${this.mode}`
            },
            // 
            colorClass: function () {
                return !!this.color ? (`segment-${this.mode}-${this.color}`) : ''
            },
        },
        methods: {
        	/**
             * 
             * onSegmentChangeparentVal
             * */
            setChildrenSelectState: function () {
                let _this = this;
                _this.$children.forEach(function (item) {
                    item.isSelect = (item.value === _this.parentVal)
                });
                _this.$emit('ionChange',_this.parentVal)
            }
        },
        mounted: function () {
            this.setChildrenSelectState();
        },
    }
</script>



