<script type="text/ecmascript-6">
  export default{
    data(){
      return {
        isDisabled: false,
      }
    },
    watch: {},
    computed: {},
    methods: {
      disableScroll(){
        const _this = this;
        if (!_this.isDisabled) {
          _this.$disableScroll(true);
          _this.isDisabled = true;
          setTimeout(function () {
            _this.$disableScroll(false);
            _this.isDisabled = false;
          },3000)
        }
      }
    },
