<template>
  <div class="ion-loading" :class="[modeClass,cssClass]">
    <!--<Backdrop :isActive="isActive" v-if="showBackdrop" :enableBackdropDismiss="false"></Backdrop>-->
    <transition name="loading"
                v-on:before-enter="_beforeEnter"
                v-on:after-enter="_afterEnter"
                v-on:before-leave="_beforeLeave"
                v-on:after-leave="_afterLeave">
      <div class="loading-wrapper" v-show="isActive">
        <div v-if="showSpinner" class="loading-spinner">
          <Spinner :name="spinner"></Spinner>
        </div>
        <div v-if="content" v-html="content" class="loading-content"></div>
      </div>
    </transition>
  </div>
</template>
<style lang="scss">
  @import './loading';
  @import './loading.ios';
  @import './loading.md';
  @import './loading.wp';
  // transition
  @import "../../transitions/loading";

</style>
<script type="text/ecmascript-6">
  export default{
    name: 'Loading',
    data(){
      return {
        /**
         * Instance
         * propdata
         * prop
         * */
        spinner: this.$config.get('spinner') || 'ios', // String
        content: null, // html
        cssClass: null,
        showBackdrop: false,
        duration: null, // 
        dismissOnPageChange: false,// 

        /**
         * 
         * */
        isActive: false, // 
        enabled: false, // false
        mode: this.$config.get('mode') || 'ios', // ios?android?window

        // promise
        presentCallback: null,
        dismissCallback: null,

      }
    },
    computed: {
      // ActionSheet
      modeClass () {
        return `loading-${this.mode}`
      },
      showSpinner () {
        return this.spinner !== 'hide'
      },
    },
    methods: {
      /**
       * ActionSheet Animate Hooks
       * */
      _beforeEnter () {
        this.enabled = false; // 
        this.$setEnabled(false, 200);
      },
      _afterEnter (el) {
        this.enabled = true;
        this.presentCallback(el);
      },
      _beforeLeave () {
        this.enabled = false;
        this.$setEnabled(false, 200);
      },
      _afterLeave (el) {
        this.enabled = true;
        this.dismissCallback(el);
        // DOM
        this.$el.remove()
      },

      /**
       * 
       * 
       * @returns {Promise} transitionEndpromise
       */
      _present () {
        const _this = this;
        _this.isActive = true;
        return new Promise((resolve) => {this.presentCallback = resolve})
      },

      /**
       * 
       * @returns {Promise} transitionEndpromise
       */
      _dismiss () {
        const _this = this;
        if (!_this.enabled) {
          return false
        }
        _this.enabled = false;
        _this.isActive = false; // 
        return new Promise((resolve) => {this.dismissCallback = resolve})
      },
    }
  }
</script>



