<template>
  <i class="icon ion-icon"
     :class="[colorClass,nameClass,itemClass]"></i>
</template>
<style lang="scss">
  @import "icon.scss";
  @import "./ionicons/css/ionicons.css";
</style>
<script type="text/ecmascript-6">
  /**
   * Icon
   *
   * iconionicons/imgClass
   *
   * 1. ionicons, nameioniconsname(ion/mode)
   *  <Icon mode="ios" name="star"></Icon>  -->  ion-ios-star
   *  <Icon name="star"></Icon>             -->  mode  ->  ion-ios-star/ion-android-star
   *
   * 2. icon, , ionicons.
   *  <Icon name="icon-star"></Icon>        -->  icon-star
   *
   * */
  export default{
    name: 'Icon',
    props: {
      /**
       * 
       * */
      mode: {
        type: String,
        default: VM.config.get('mode', 'ios') || 'ios',
      },
      /**
       * color
       * primary:    #007aff,
       * secondary:  #32db64,
       * danger:     #d91e18,
       * light:      #f4f4f4,
       * dark:       #222,
       * */
      color: {
        type: String,
        default: '',
      },

      /**
       * icon, 
       * */
      name: {
        type: String,
        default: '',
      },

      /**
       * ,
       * ,:
       * ion -> `${this.name}-outline`
       * */
      activeName: {
        type: String,
        default: '',
      },

      /**
       * 
       *
       * :
       * activeIcon, 
       *
       * namenormal
       * activeName
       *
       * */
      isActive: {
        type: Boolean,
        default: false,
      },

    },
    data(){
      return {
        nameClass: '', // nameClass
        itemClass: '',

        nameValue: '', // 
        activeNameValue: '', // 

      }
    },
    watch: {
      isActive () {
        this.init();
      },
    },
    computed: {
      // 
      colorClass () {
        return !!this.color ? (`icon-${this.color}`) : ''
      },
    },
    methods: {

      // -------- private ---------

      /**
       * name
       * icon-star  ->    icon-star
       * star       ->    ion-star
       * (ios)star  ->    ion-ios-star
       * */
      getFilteredName(){
        let _name = this.name;
        if (_name.indexOf('icon-') === 0) {
          return _name
        } else {
          return `ion-${this.mode}-${_name}`
        }
      },

      /**
       * activeName
       * */
      getFilteredActiveName(){
        let _activeName = this.activeName;
        if (!!_activeName) {
          return _activeName
        } else {
          let _name = this.name;
          if (_name.indexOf('icon-') === 0) {
            return _name
          } else {
            return `ion-${this.mode}-${_name}-outline`
          }
        }
      },

      /**
       * 
       * */
      init(){

        /**
         * name
         * */
        this.nameValue = this.getFilteredName();
        this.activeNameValue = this.getFilteredActiveName();

        /**
         * 
         */
        if (this.isActive) {
          this.nameClass = this.activeNameValue;
        } else {
          this.nameClass = this.nameValue;
        }
      },
    },
    created(){
      this.init();
    },
    mounted(){

      if (!!this.$parent.$el && this.$parent.$el.className && this.$parent.$el.className.indexOf('item') > -1) {
        //	button in items should add class of 'item-button'
        this.itemClass += 'item-icon';
      }

    }
  }
</script>



