<template>
  <div :class="[
  itemClass,
  listHeaderClass,
  colorClass,
  {'input-has-value':inputHasValue},
  {'input-has-focus':inputHasFocus},
  {'item-input':isInput},
  {'item-textarea':isTextarea},
  {'item-label-fixed':isFixed},
  {'item-label-floating':isFloating},
  {'item-label-stacked':isStacked},
  {'item-label-inset':isInset}]" @click="directTo()">
    <!--[item-left],ion-checkbox:not([item-right])-->
    <slot name="left"></slot>

    <!--<ng-content select="[item-left],ion-checkbox:not([item-right])"></ng-content>-->
    <div class="item-inner">
      <div class="input-wrapper">
        <!--ion-labelion-label-->
        <slot></slot>
        <!--<Label>-->
        <slot name="label"></slot>
        <!--</Label>-->
        <!--ion-select,ion-input,ion-textarea,ion-datetime,ion-range,[item-content]-->
        <slot name="content"></slot>
      </div>

      <!--[item-right],ion-radio,ion-toggle-->
      <slot name="right"></slot>
      <!--<ion-reorder *ngIf="_shouldHaveReorder"></ion-reorder>-->
    </div>
    <div class="button-effect"></div>
  </div>
</template>
<style lang="scss">
  /*@import "item";*/
  /*@import "item.ios";*/
  /*@import "item.md";*/
  /*@import "item.wp";*/
  /*@import "item-media";*/
  /*@import "item-reorder";*/
  /*@import "item-sliding";*/
</style>
<script type="text/ecmascript-6">
  module.exports = {
    data(){
      return {
        inputHasValue: false, // input, input
        inputHasFocus: false, // input, input
        isInput: false, // input, input
        isTextarea: false, // input, textarea

        // label
        isFixed:false,
        isFloating:false,
        isStacked:false,
        isInset:false,
      }
    },
    props: {
      /**
       * mode  ios/window/android/we/alipay
       * */
      mode: {
        type: String,
        default: VM.config.get('mode') || 'ios',
      },
      /**
       * colorprimarysecondarydangerlightdark
       * */
      color: {
        type: String,
        default: '',
      },
      /**
       * router-linka
       * */
      to: {
        type: Object,
        default () {
          return {}
        },
      }
    },
    watch: {
    },
    computed: {
      // 
      itemClass () {
        return `item item-${this.mode}`
      },

      // 
      colorClass () {
        return !!this.color ? (`label-${this.mode}-${this.color}`) : ''
      },
    },
    methods: {
      /**
       * a
       * */
      directTo(){
        if (!!this.to) {
          this.$router.push(this.to)
        }
      }
    },
    created () {

    },
    beforeMount(){

    },
    mounted () {
      // slot="left"/slot="right"
      if (!!this.$slots && !!this.$slots['left']) {
        this.$slots['left'].forEach(function (item) {
          item.elm.setAttribute('item-left', '')
        })
      }
      if (!!this.$slots && !!this.$slots['right']) {
        this.$slots['right'].forEach(function (item) {
          item.elm.setAttribute('item-right', '')
        })
      }

    }
  }
</script>



