<template>
  <label class="ion-label label" :class="[modeClass,colorClass]" :form="form">
    <slot></slot>
  </label>
</template>
<style lang="scss">
  @import "./label.scss";
  @import "./label.ios.scss";
  /*@import "./label.md.scss";*/
  /*@import "./label.wp.scss";*/
</style>
<script type="text/ecmascript-6">
  export default{
    name: 'Label',
    data(){
      return {
        _item: null, // Item
      }
    },
    props: {
      /**
       * mode  ios/window/android/we/alipay
       * */
      mode: {
        type: String,
        default: VM.config.get('mode') || 'ios',
      },
      /**
       * colorprimarysecondarydangerlightdark
       * */
      color: {
        type: String,
        default: '',
      },

      /**
       *  label 
       * */
      form: {
        type: String,
        default: '',
      },

      /**
       *  label 
       * */
      for: {
        type: String,
        default: '',
      }

    },
    watch: {},
    computed: {
      // 
      modeClass () {
        return `label-${this.mode}`
      },
      // 
      colorClass () {
        return !!this.color ? (`label-${this.mode}-${this.color}`) : ''
      }

    },
    methods: {},
    created () {},
    mounted () {
      const _this = this;
      // ItemClass
      this._item = this.$parent;

      /**
       * fixed/floating/stackedinputlabel
       * fixed: label, input
       * floating:  input, labelinput; input, 
       * stacked: floating, , 
       * */
      if (this.$el.getAttribute('fixed') !== null) {
        this._item.isFixed = true;
      }
      if (this.$el.getAttribute('floating') !== null) {
        this._item.isFloating = true;
      }
      if (this.$el.getAttribute('stacked') !== null) {
        this._item.isStacked = true;
      }
      if (this.$el.getAttribute('inset') !== null) {
        this._item.isInset = true;
      }

    },
    activated () {},
    components: {}
  }
</script>



